import React, { useState } from 'react';
import './SubmitForm.css';
import { Button } from '@mui/material';
import { useLocation } from 'react-router-dom';

export default function SubmitForm() {
  const [selectedImage, setSelectedImage] = useState('https://imgs.search.brave.com/ZKPVwCQPLZGIqAz1fDOewRBzi_zg-BPECn7Ui9XdUcM/rs:fit:500:0:0:0/g:ce/aHR0cHM6Ly90My5m/dGNkbi5uZXQvanBn/LzA3Lzg1Lzk3Lzkw/LzM2MF9GXzc4NTk3/OTAwOV9CQzBMUzR0/NWpnZFJ3OERsaDFN/VkFGSUtKTjF4Z2ZI/OC5qcGc');
  const [values, setValues] = useState({
    attachment: '',
  });
  const location = useLocation();
  const json_Data = location.state;
  console.log(json_Data)
  let final_data = {
    personInfo: json_Data.data_to_submit.data_to_send.dataOfPerson[0],
    plans: json_Data.data_to_submit.data_to_send.dataOfPerson[1],
    entertainment: json_Data.data_to_submit.data_to_send.dataOfPerson[2],
    profilePicture: json_Data.data_to_submit.data_to_send.dataOfPerson[3],
    screenshot: values.attachment,
  }; 
  console.log(json_Data)
  console.log("THE DP DATA IS :",final_data.profilePicture.profile_picture)
  console.log("THE SCREENSHOT DATA DATA IS :",final_data.screenshot)
  function POSTDATA(event) {
    event.preventDefault();
    console.log(final_data.profilePicture);
    const formData = new FormData();
    formData.append('dp', final_data.profilePicture.profile_picture);
    formData.append('screenshot', values.attachment);
    formData.append('user_data', JSON.stringify(final_data));

    fetch('http://localhost:8000/signup', {
      method: 'POST',
      body: formData,
    })
      .then((response) => response.text())
      .then((message) => console.log(message))
      .catch((error) => console.error(error));
  }

  console.log(final_data);

  const handleChange = (event) => {
    const { name, value, files } = event.target;
    if (files) {
      const file = files[0];
      if (file) {
        const reader = new FileReader();
        reader.onloadend = () => {
          setSelectedImage(reader.result);
          setValues((prevValues) => ({ ...prevValues, attachment: file }));
        };
        reader.readAsDataURL(file);
      }
    } else {
      setValues({ ...values, [name]: value });
    }
  };

  return (
    <div id="submit_form_container">
      <article>
        <h2>Attach Screenshot of Payment!</h2>
        <p>Please attach the screenshot of the payment that you did on your respective UPI application.</p>
      </article>
      {/* File input */}
      <input
        type="file"
        name="attachment"
        id="attachment"
        accept="image/*"
        style={{ display: 'none' }}
        onChange={handleChange}
      />
      {/* Image holder to display the selected image */}
      <section id="image_holder2">
        <label htmlFor="attachment">
          <img src={selectedImage} alt="Selected Profile" />
        </label>
        <br />
        <br />
      </section>
      <Button variant="contained" color="secondary" onClick={POSTDATA}>
        Submit
      </Button>
    </div>
  );
}
