import React from 'react'
import Hr from './Hr.js';
import './FormPart1.css';
import { Button, TextField } from '@mui/material';
import MenuItem from '@mui/material/MenuItem';
import swipe from './Swipe_functions.js';
import { useState } from 'react';
import { useTheme } from '@emotion/react';

export default function FormPart1(props) {
    const [values, setValues] = useState({
        fullname: '',
        phoneNumber: '',
        email: '',
        collge:'',
        course:'',
        year:'',
      });
      const [btnState,setBtnState]=useState(()=>true)
      const sendData = () => {
        const data = values;
        // Call the function passed from the parent and send the data
        props.sendDataToParent(data);
      };
      function testValues(){
        console.log(values.fullname,values.phoneNumber,values.email,values.collge,values.course,values.year)
        sendData();
        swipe(1)
      }
      const handleChange = (event) => {
        const { name, value } = event.target;
        setValues({
          ...values,      // Copy existing values
          [name]: value   // Update the value of the specific field that changed
        });
        form_validation()
      };
      function form_validation(){
        if(values.fullname == "" || values.phoneNumber=="" || values.email=="" || values.year=="" || values.course=="" || values.collge==""){
            setBtnState(true)
        }
        else{
            setBtnState(false)
        }
      }
    const courses = [
        "MBBS", "BDS", "MD/MS/DNB", "Mch/DM", "B.Pharma", "B.Nursing", "BPT"
    ]
    const years = [
        2014, 2015, 2016, 2017, 2018, 2019, 2020, 2021, 2022, 2023, 2024
    ]
    return (
        <div id="formpart1_container" className='first_slide'>
            <article>
                <h2>Basic details</h2>
                <p>
                    Please fill your personal details in the below mentioned form.
                </p>
            </article>
            <Hr />
            <section className='text_holder'>
                <TextField variant='outlined' color="secondary" label="Full name" fullWidth onChange={handleChange} value={values.fullname} name="fullname"/>
            </section>
            <section className='text_holder'>
                <TextField variant='outlined' color="secondary" label="Phone number" fullWidth onChange={handleChange} value={values.phoneNumber} name="phoneNumber"/>
            </section>
            <section className='text_holder'>
                <TextField variant='outlined' color="secondary" label="Email address" fullWidth onChange={handleChange} value={values.email} name="email"/>
            </section>
            <section className='text_holder' id="select_holder">
                <TextField
                    id="outlined-select-currency"
                    select
                    label="Select"
                    defaultValue="MBBS"
                    helperText="Select your Course"
                    placeholder='Select'
                    onChange={handleChange} value={values.course}
                    name="course"
                >
                    {
                        courses && courses.map((item) => (
                            <MenuItem key={item} value={item}>{item}</MenuItem>
                        ))
                    }
                </TextField>
                <TextField
                    id="outlined-select-year"
                    select
                    defaultValue={2021}
                    label="year"
                    helperText="Select your year"
                    onChange={handleChange} value={values.year}
                    name="year"
                >
                    {
                        years && years.map((item) => (
                            <MenuItem key={item} value={item}>{item}</MenuItem>
                        ))
                    }
                </TextField>
            </section>
            <section className='text_holder'>
                <TextField variant='outlined' color="secondary" label="College" fullWidth onChange={handleChange} value={values.collge} name="collge"/>
            </section>
            <Hr />
            <section className='text_holder'>
                <Button variant='contained' color='secondary' fullWidth onClick={()=>testValues()} >Next</Button>
            </section>
        </div>
    )
}
